import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import { AccordionItem } from "carbon-components-react";

const dispatchedEvents = ["ABOUT#clicked"];

/*
    Example: <srs-accordion>
                <srs-accordion-item title="Item 1">
                    <p>Content of Item 1</p>
                </srs-accordion-item>
                <srs-accordion-item title="Item 2">
                    <p>Content of Item 2</p>
                </srs-accordion-item>
            </srs-accordion>
*/
const SrsAccordionItem = function (props, ref) {
    return (
        <AccordionItem {...props}>
          <slot/>
        </AccordionItem>
    );
}

const styles = `
    
`
const props = [{
    name: "title",
    defaultValue: "Accodion Item1",
    type: "string"
}]

const slots = [{name: "default"}]; 

const WrappedComponent = wrapper(React.forwardRef(SrsAccordionItem), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };